import React from "react";
import { NavLink } from "react-router-dom";
import "./NavBar.css";

const NavBar = () => {

  return (
    <header>
      <p className="cabecera">
        Visual Design Portfolio
      </p>
      <nav>
        <ul>
          <li>
            <NavLink to="/mockups" exact basename={process.env.PUBLIC_URL}>
              Mockups
            </NavLink>
          </li>
          <li>
            <NavLink to="/images" exact basename={process.env.PUBLIC_URL}>Images</NavLink>
          </li>
          <li>
            <NavLink to="/gif" exact basename={process.env.PUBLIC_URL}>Animated GIF</NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
};
export default NavBar;